@use "global" as *;

.contact-page__inner {
    position: relative;
}

.contact-page__error {
        margin: 0 auto;
        text-align: left;
    @include mq(md){
        max-width: rem(700);
        text-align: center;
    }
}

.contact-page__error-message {
    color: #C94800;
    font-size: rem(14);
    line-height: 1.5;
    max-width: rem(224);
    width: 100%;
    margin: 0 auto;
}

.contact-page__content {
    position: relative;
    z-index: 10;
    @include mq(md){
        max-width: rem(700);
        margin: 0 auto;
    }
}

.contact-page__content--error {
    margin-top: rem(40);
    @include mq(md){
        margin: rem(40) auto 0;
    }
}

.contact-page__complete {
    margin: 0 auto;
    text-align: center;
    @include mq(md){
        max-width: rem(622);
    }
}

.contact-page__message {
    line-height: 1.45;
}

.contact-page__message + .contact-page__message {
    margin-top: rem(33);
}